@tailwind base;
@tailwind components;
@tailwind utilities;

body {
  font-family: "Open Sans", sans-serif;
}

@layer components {
  .tipTop {
    position: absolute;
    top: -32px;
    right: -14px;
    width: 32px;
    padding: 2px 0;
    font-size: 10px;
    color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 5px;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    -ms-border-radius: 5px;
    -o-border-radius: 5px;
  }
  .tipTopAfter {
    position: absolute;
    top: -16px;
    right: -5px;
    width: 0;
    border: 7px solid transparent;
  }
}
